.shopItem {
  display: flex;
  padding: 10px;
}

.shopItem__pic {
  border-radius: 2px;
  width: 66px;
  height: 66px;
  margin-right: 10px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  flex-shrink: 0;
  position: relative;
  box-sizing: border-box;
}

.shopItem__content {
  flex: 1;
}

.shopItem__title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 16px;
  font-weight: 400;
  color: #323232;
  margin-right: 3px;
}

.shopItem__comment {
  font-size: 0;
  margin: 8px 0;
  position: relative;
}

.shopItem__star {
  display: inline-block;
  width: 66px;
  height: 12px;
  vertical-align: middle;
  background: url("data:image/png;base64,")
    no-repeat;
  background-size: 86px auto;
}

.shopItem__star--50 {
  background-position: 0px -15px;
}

.shopItem__star--45 {
  background-position: 0px -32px;
}

.shopItem__star--40 {
  background-position: 0px -49px;
}

.shopItem__star--35 {
  background-position: 0px -66px;
}

.shopItem__star--30 {
  background-position: 0px -83px;
}

.shopItem__star--20 {
  background-position: 0px -100px;
}

.shopItem__star--10 {
  background-position: 0px -117px;
}

.shopItem__quantity {
  font-size: 12px;
  color: #333;
  line-height: 12px;
  margin-left: 12px;
  vertical-align: bottom;
}

.shopItem__price {
  display: inline-block;
  font-size: 12px;
  color: #333;
  line-height: 12px;
  margin-left: 12px;
  vertical-align: bottom;
}

.shopItem__price::before {
  content: "\A5";
  font-size: 12px;
  line-height:12px;
  margin-right: 2px;
  vertical-align: bottom;
  display: inline;
}

.shopItem__info {
  font-size: 12px;
  color: #999;
  line-height: 12px;
}

.shopItem__region {
  margin-right: 10px;
}
